
import XButton from 'vux/src/components/x-button/index.vue'


export default {
name: 'List',
components: {
XButton,
},
data() {
return {
data: [
{name: "", id: 5},
{name: "", id: 4},
{name: "", id: 2},
{name: "", id: 8},
{name: "", id: 9},
]
}
},
methods: {
show: function (index) {
this.$vux.toast.show({
text: index + '  ' + this.data[index].name,
type: 'text',
position: 'bottom'
})
},
addData: function () {
let a = {name: "", id: 10}
this.data.push(a)
},
deleteData: function () {
this.data.pop()
},
replaceData: function () {
let newData = [{name: "1", id: 1}, {name: "2", id: 2}, {name: "3", id: 3}]
this.data = newData
},
clean: function () {
this.data = []
}
}
}



